'use client'

import { createContext, useContext, useState, useEffect } from 'react'
import { 
  signInWithEmailAndPassword, 
  createUserWithEmailAndPassword,
  signOut as firebaseSignOut,
  onAuthStateChanged,
  OAuthProvider,
  signInWithPopup,
  setPersistence,
  browserLocalPersistence,
  User
} from 'firebase/auth'
import { auth, initializeFirebase } from '../config/firebase'
import { createUserFolder } from '../services/api'

interface AuthContextType {
  user: User | null
  signIn: (email: string, password: string) => Promise<{ success: boolean; user?: User; error?: string }>
  signUp: (email: string, password: string) => Promise<{ success: boolean; user?: User; error?: string }>
  signInWithMicrosoft: () => Promise<{ success: boolean; user?: User; error?: string }>
  signOut: () => Promise<{ success: boolean; error?: string }>
  loading: boolean
}

const AuthContext = createContext<AuthContextType>({
  user: null,
  signIn: async () => ({ success: false }),
  signUp: async () => ({ success: false }),
  signInWithMicrosoft: async () => ({ success: false }),
  signOut: async () => ({ success: false }),
  loading: true,
})

export function useAuth() {
  return useContext(AuthContext)
}

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    let unsub = () => {}

    const setupAuth = async () => {
      // Asegurar que Firebase est√© inicializado
      await initializeFirebase()

      // Re-importar auth para obtener la instancia actualizada
      const { auth: currentAuth } = await import('../config/firebase')

      if (!currentAuth) {
        setLoading(false)
        return
      }

      try {
        await setPersistence(currentAuth, browserLocalPersistence)
      } catch (e) {
        console.warn('No se pudo establecer persistencia local:', (e as Error).message || e)
      }

      unsub = onAuthStateChanged(currentAuth, async (user) => {
        setUser(user)
        setLoading(false)
        if (user) {
          // Guardar token en cookie para el middleware
          try {
            const token = await user.getIdToken()
            document.cookie = `firebase-token=${token}; path=/; max-age=3600; SameSite=Lax`
          } catch (e) {
            console.warn('Error guardando token en cookie:', (e as Error).message || e)
          }
          
          try {
            createUserFolder().catch((e) => console.warn('No se pudo crear carpeta al iniciar sesi√≥n:', (e as Error).message || e))
          } catch (e) {
            console.warn('Error invocando createUserFolder en onAuthStateChanged:', (e as Error).message || e)
          }
        } else {
          // Eliminar cookie al cerrar sesi√≥n
          document.cookie = 'firebase-token=; path=/; max-age=0'
        }
      })
    }

    setupAuth()

    return () => {
      try { unsub() } catch (e) {}
    }
  }, [])

  async function signIn(email: string, password: string) {
    await initializeFirebase()
    const { auth: currentAuth } = await import('../config/firebase')
    if (!currentAuth) {
      return { success: false, error: 'Firebase Auth no est√° disponible' }
    }
    try {
      const userCredential = await signInWithEmailAndPassword(currentAuth, email, password)
      return { success: true, user: userCredential.user }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  }

  async function signInWithMicrosoft() {
    await initializeFirebase()
    const { auth: currentAuth } = await import('../config/firebase')
    if (!currentAuth) {
      return { success: false, error: 'Firebase Auth no est√° disponible. Verifica las variables de entorno.' }
    }
    try {
      const provider = new OAuthProvider('microsoft.com')
      provider.addScope('openid')
      provider.addScope('profile')
      provider.addScope('email')
      provider.addScope('offline_access')
      provider.addScope('Files.ReadWrite.All')

      const result = await signInWithPopup(currentAuth, provider)
      try {
        await createUserFolder()
      } catch (e) {
        console.warn('No se pudo crear carpeta en OneDrive tras login con Microsoft:', (e as Error).message || e)
      }

      return { success: true, user: result.user }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  }

  async function signUp(email: string, password: string) {
    await initializeFirebase()
    const { auth: currentAuth } = await import('../config/firebase')
    if (!currentAuth) {
      return { success: false, error: 'Firebase Auth no est√° disponible' }
    }
    try {
      const userCredential = await createUserWithEmailAndPassword(currentAuth, email, password)
      try {
        await createUserFolder()
      } catch (e) {
        console.warn('No se pudo crear la carpeta en OneDrive al registrar usuario:', (e as Error).message || e)
      }
      return { success: true, user: userCredential.user }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  }

  async function signOut() {
    await initializeFirebase()
    const { auth: currentAuth } = await import('../config/firebase')
    if (!currentAuth) {
      return { success: false, error: 'Firebase Auth no est√° disponible' }
    }
    try {
      await firebaseSignOut(currentAuth)
      // Eliminar cookie al cerrar sesi√≥n
      document.cookie = 'firebase-token=; path=/; max-age=0'
      return { success: true }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  }

  const value = {
    user,
    signIn,
    signUp,
    signInWithMicrosoft,
    signOut,
    loading
  }

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  )
}

